const Navbar= () =>{
    var dataU;

  useEffect(() => {
    fetch("/movies").then(response => response.json().then(data=> dataU = data));
  }, []);
  const sleep = (milliseconds) => {
    return new Promise(resolve => setTimeout(resolve, milliseconds))
  }
  const[query,setQuery] = useState("");
  let text1= "https://72f4-99-250-243-69.ngrok.io/search?url="
  const urlFull= text1.concat(query);
  const handleClick = () => {
    console.log(window.window.innerWidth);
    if (window.window.innerWidth > 600)  {
        fetch(urlFull).then(response => response.text().then(text=> { console.log(text);}));
        sleep(1500).then(()=> {
          window.location.reload(false);
        })
    }
    
    }
    
    sleep(100).then(()=> {
      const good = JSON.stringify(dataU);
      console.log(good);
    })

    


    const [open,setOpen]=useState(false);
    const classes = useStyles( {open});
    return <AppBar position="static">
        <Toolbar className={classes.toolbar}>
            <Typography variant="h6" className={classes.logo}>YT Analytics</Typography>
            <div className={classes.search}>
            <SearchIcon/>
            <InputBase placeholder="Cut & Paste Video URL..." className={classes.input}  onChange={e => setQuery(e.target.value)} 
            onKeyPress={(ev) => {
                console.log(`Pressed keyCode ${ev.key}`);
                if (ev.key === 'Enter') {
                    fetch(urlFull).then(response => response.text().then(text=> { console.log(text);}));
                    sleep(1500).then(()=> {
                      window.location.reload(false);
                    })
                  ev.preventDefault();
                }
              }}/>
            
        </div>
        
        <div className={classes.icon}>
        <Button variant="contained" className={classes.test} onClick={handleClick}  >Search</Button>
        <Button variant="contained" className={classes.test2}>Random Video<AutoFixHighRoundedIcon/></Button>
        </div>
        </Toolbar>
        
    </AppBar>

}

export default Navbar;

